.archive {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.5rem;
}

.archive > * {
  margin-bottom: 1.5rem;
  padding-left: 1.5rem;
  width: 100%;
}

@media (min-width: 38em) {
  .archive > * {
    width: 50%;
  }
}

.archive figure img {
  border: 1px solid #ddd;
  border-bottom: 0;
}

.archive figcaption {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
  padding-top: .5rem;
}

.archive figcaption a {
  margin-bottom: .75rem;
  display: block;
}

